/* @ngInject */
export default function appConfig ($locationProvider, $stateProvider, $urlRouterProvider) {
  $locationProvider.html5Mode(false)

  $stateProvider
    .state('app', {
      abstract: true,
      component: 'appComponent'
    })

  $urlRouterProvider.otherwise('/')
}



